.App {
  text-align: center;
  margin: 20px;
  background-color: rgb(163, 204, 163);
  min-height: 900px;
}

textarea, input {
  width: 80%;
  margin: 10px 0 0 0;
  padding: 10px;
  border-radius : 5px;
}

button {
  background-color: blueviolet;
  border-color: purple;
  color : gold;
  margin: 0 10px 0px 0px;
  font-size: 20px;
  
}



pre {
  display: inline-block;
  width : 80%;
  padding: 10px;
  margin : 1px;
  background-color: #f4f4f4;
  border: 1px solid #ccc;

  text-align: left;

}

.tranBtn {
  float: left;
  background-color: teal;
  color : white;
  border-color : teal;

}
.copyBtn {

  background-color: chocolate;
  color : white;
  border-color : chocolate;
}

.rplBtn {

  background-color: cornflowerblue;
  color : white;
  border-color : cornflowerblue;
}


table {
  width : 80%;
  text-align: right;
  display: inline-table;
  
}

input {
  width : 15%;
  padding : 3px;
}



.memo {
  text-align: right;
  font-size: 10px;
  color:blue;
  line-height: 200%;
  white-space : nowrap;
}

p {
  line-height: 0.2rem;
}
